// Common general rules and resets:
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

// General settings:
body {
  background-color: pink;
  min-height: 100vh;

  font-family: sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  color: black;
}
